import * as React from 'react';

import { useState, useEffect } from "react";
import {useNavigate} from 'react-router';

// react-router-dom components
import { Link } from "react-router-dom";

// @mui material components
import Card from "@mui/material/Card";
import Switch from "@mui/material/Switch";
import Grid from "@mui/material/Grid";
import MuiLink from "@mui/material/Link";

// @mui icons
import FacebookIcon from "@mui/icons-material/Facebook";
import GitHubIcon from "@mui/icons-material/GitHub";
import GoogleIcon from "@mui/icons-material/Google";

// Material Dashboard 2 React components
import MDBox from "components/MDBox";
import MDTypography from "components/MDTypography";
import MDInput from "components/MDInput";
import MDButton from "components/MDButton";

// Authentication layout components
import BasicLayout from "layouts/authentication/components/BasicLayout";

// Images
import bgImage from "assets/images/bg-sign-in-basic.jpeg";
import axios from "axios";

function Basic() {
  const [rememberMe, setRememberMe] = useState(false);
  const [posts, setPosts] = useState([]);
  const [totalPage, setTotalPage] = useState(0);
  const [userName, setUserName] = useState('');
  const [password, setPassword] = useState('');
  const [open, setOpen] = React.useState(false);
  const [dialogTitle, setDialogTitle] = React.useState('');
  const [dialogMessage, setDialogMessage] = React.useState('');
  const navigate = useNavigate();

  const handleSetRememberMe = () => setRememberMe(!rememberMe);

  const handleSignIn = (event) => {
    console.log(userName);
    console.log(password);

    axios({
      url: '/api/sns/users/login',
      method: 'POST',
      data: {
        name: userName,
        password: password,
      },
    })
        .then((res) => {
          console.log('success');
          setDialogTitle('success');
          setDialogMessage('');
          setOpen(true);
          localStorage.setItem('token', res.data.result.token);
          console.log(res.data.result.token);
          handleGetTables(0);
        })
        .catch((error) => {
          setDialogTitle(error.response.data.resultCode);
          setDialogMessage(error.response.data.resultMessage);
          setOpen(true);
          console.log(error);
        });
  };

  const handleGetTables = (pageNum, event) => {
    console.log('handleGetTables');
    axios({
      url: '/api/board?size=5&sort=id&page=' + pageNum,
      method: 'GET',
      headers: {
        Authorization: 'Bearer ' + localStorage.getItem('token'),
      },
    })
        .then((res) => {
          console.log('success');
          console.log(res);
          setPosts(res.data.result.content);
          setTotalPage(res.data.result.totalPages);
          navigate('/tables');
        })
        .catch((error) => {
          console.log(error);
          navigate('/authentication/sign-in');
        });
  };

  useEffect(() => {
    handleGetTables(0);
  }, []);

    return (
        <BasicLayout image={bgImage}>
          <Card>
            <MDBox
                variant="gradient"
                bgColor="info"
                borderRadius="lg"
                coloredShadow="info"
                mx={2}
                mt={-3}
                p={2}
                mb={1}
                textAlign="center"
            >
              <MDTypography variant="h4" fontWeight="medium" color="white" mt={1}>
                Sign in
              </MDTypography>
              <Grid container spacing={3} justifyContent="center" sx={{mt: 1, mb: 2}}>
                <Grid item xs={2}>
                  <MDTypography component={MuiLink} href="#" variant="body1" color="white">
                    <FacebookIcon color="inherit"/>
                  </MDTypography>
                </Grid>
                <Grid item xs={2}>
                  <MDTypography component={MuiLink} href="#" variant="body1" color="white">
                    <GitHubIcon color="inherit"/>
                  </MDTypography>
                </Grid>
                <Grid item xs={2}>
                  <MDTypography component={MuiLink} href="#" variant="body1" color="white">
                    <GoogleIcon color="inherit"/>
                  </MDTypography>
                </Grid>
              </Grid>
            </MDBox>
            <MDBox pt={4} pb={3} px={3}>
              <MDBox component="form" role="form">
                <MDBox mb={2}>
                  <MDInput type="userName" label="User Name"
                           onChange={(v) => setUserName(v.target.value)}
                           fullWidth/>
                </MDBox>
                <MDBox mb={2}>
                  <MDInput type="password" label="Password"
                           onChange={(v) => setPassword(v.target.value)}
                           fullWidth/>
                </MDBox>
                <MDBox display="flex" alignItems="center" ml={-1}>
                  <Switch checked={rememberMe} onChange={handleSetRememberMe}/>
                  <MDTypography
                      variant="button"
                      fontWeight="regular"
                      color="text"
                      onClick={handleSetRememberMe}
                      sx={{cursor: "pointer", userSelect: "none", ml: -1}}
                  >
                    &nbsp;&nbsp;Remember me
                  </MDTypography>
                </MDBox>
                <MDBox mt={4} mb={1}>
                  <MDButton onClick={handleSignIn} variant="gradient" color="info" fullWidth>
                    sign in
                  </MDButton>
                </MDBox>
                <MDBox mt={3} mb={1} textAlign="center">
                  <MDTypography variant="button" color="text">
                    Don&apos;t have an account?{" "}
                    <MDTypography
                        component={Link}
                        to="/authentication/sign-up"
                        variant="button"
                        color="info"
                        fontWeight="medium"
                        textGradient
                    >
                      Sign up
                    </MDTypography>
                  </MDTypography>
                </MDBox>
              </MDBox>
            </MDBox>
          </Card>
        </BasicLayout>
    );
}

export default Basic;
